<template>
  <div>
    <table>
      <tr class="head">
        <th>Document Name</th>
        <th>Document Type</th>
        <th>IRN</th>
        <th>Download</th>
      </tr>
      <tr class="body" v-for="file in files" :key="file.id">
        <td>{{parseName(file.formData.fileName)}}</td>
        <td>{{parseName(file.formData.documentType)}}</td>
        <td>{{parseName(file.formData.IRN)}}</td>
        <td><v-icon name="cloud-download-alt" /></td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: "Table",
  props: {
    files: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    parseName(fileName) {
      if (fileName.includes("_")) {
        const newName = fileName.split("_");
        newName.shift();
        const finalName = newName.join(" ");
        return finalName;
      } else {
        return fileName;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/scss/variables";
div {
    padding: 10px;
}
table {
  width: 100%;
  padding: 10px;
  border: 1px solid black;
  border-collapse: collapse;
  .head,
  .body {
    th,
    td {
      border: 1px solid black;
      border-collapse: collapse;
      margin: 0;
    }
  }
  .head,
  .body {
    text-align: left;
  }
  .head {
      background-color: $secondary;
      font-size: $font-size-large;
  }
  .body {
      td {
          padding: 5px;
          text-transform: capitalize;
      }
  }
}
</style>