<template>
  <div>
    <section class="doc-tree-nav-choice" @click="docTreeSwitch()">
      <v-icon v-if="!openDocTree" name="caret-right" />
      <v-icon v-if="openDocTree" name="caret-down" />
      <v-icon name="tree" style="marginLeft: 5px" />
      <div class="doctree-name">DocTree</div>
    </section>
    <transition name="drop-down">
      <section v-if="openDocTree" class="doc-tree-wrapper">
        <div class="doc-tree">
          <section
            class="folder-wrap"
            @click="openFolders.trans = !openFolders.trans"
          >
            <v-icon v-if="!openFolders.trans" name="folder" />
            <v-icon v-if="openFolders.trans" name="folder-open" />
            <div>Transcripts</div>
          </section>
          <template v-if="openFolders.trans">
            <section
              class="file-wrap"
              v-for="file in Transcripts"
              :key="file.ID"
            >
              <v-icon name="file" />
              <div>
                {{ file.formData.fileName.split("_")[1].slice(0, 10) + "..." }}
              </div>
            </section>
          </template>
          <section
            class="folder-wrap"
            @click="openFolders.isirs = !openFolders.isirs"
          >
            <v-icon v-if="!openFolders.isirs" name="folder" />
            <v-icon v-if="openFolders.isirs" name="folder-open" />
            <div>Isirs</div>
          </section>
          <template v-if="openFolders.isirs">
            <section class="file-wrap" v-for="file in ISIRS" :key="file.ID">
              <v-icon name="file" />
              <div>
                {{ file.formData.fileName.split("_")[1].slice(0, 10) + "..." }}
              </div>
            </section>
          </template>
        </div>
      </section>
    </transition>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "DocTree",
  data() {
    return {
      openDocTree: true,
      Transcripts: [],
      ISIRS: [],
      openFolders: {
        trans: false,
        isirs: false
      }
    };
  },
  async mounted() {
    try {
      const trans = await axios.get(
        "/api/v1/topcoder/documents/Transcripts"
      );
      const isirs = await axios.get(
        "/api/v1/topcoder/documents/ISIRS"
      );
      this.Transcripts = trans.data.Items;
      this.ISIRS = isirs.data.Items;
    } catch (err) {
      console.error(err);
    }
  },
  methods: {
    docTreeSwitch() {
      this.openDocTree = !this.openDocTree;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/scss/variables";
.doc-tree-wrapper {
  margin-top: 10px;
  padding-right: 5px;
}
.doc-tree {
  height: 100%;
  width: 100%;
  background: $gray;
  color: $font-dark;
  padding: 10px;
}
.doc-tree-nav-choice {
  display: flex;
  align-items: center;
  padding: 10px 0px 10px 8px;
  &:hover {
    cursor: pointer;
    border-left: 5px solid $secondary;
  }
  div {
    margin-left: 5px;
  }
}

.folder-wrap {
  display: flex;
  align-items: center;
  padding: 3px;
  cursor: pointer;
  div {
    margin-left: 5px;
    font-size: $font-size-medium;
  }
}

.file-wrap {
  display: flex;
  align-items: center;
  padding: 3px 3px 3px 25px;
  cursor: pointer;
  div {
    margin-left: 5px;
    font-size: $font-size-medium;
  }
}

/* drop down menu animation */
.drop-down-enter-active {
  animation: drop-down 0.6s;
}

.drop-down-leave-active {
  animation: drop-down 0.6s reverse;
}

@keyframes drop-down {
  from {
    transform: scaley(0) translate3d(0, -100%, 0);
    height: 0;
  }
  to {
    transform: scaley(1) translate3d(0, 0, 0);
  }
}
</style>
