<template>
  <div class="login-wrapper">
    <section class="login">
      <div class="logo-wrap">
        <uopxLogo />
      </div>
      <h1>UoPX DocuMynt</h1>
      <form class="login-form" @submit.prevent="login">
        <h3 class="error">{{ error }}</h3>
        <div class="input-wrapper">
          <label class="input-label">Email:</label>
          <input type="text" v-model="$v.email.$model" />
        </div>
        <div class="input-wrapper">
          <label class="input-label">Password:</label>
          <input type="password" v-model="$v.password.$model" />
        </div>
        <button class="login-button" type="submit">
          Submit
        </button>
      </form>
    </section>
  </div>
</template>

<script>
import uopxLogo from "@/assets/svgs/uopxLogo";
import { required, minLength, email } from "vuelidate/lib/validators";
import axios from "axios";

export default {
  name: "Login",
  components: {
    uopxLogo
  },
  data() {
    return {
      email: null,
      password: null,
      error: "",
      formHandling: {
        uiState: "submit not clicked",
        errors: false
      }
    };
  },
  validations: {
    email: {
      email
    },
    password: {
      minLength: minLength(4)
    }
  },
  methods: {
    async login() {
      const config = {
        method: "post",
        url: "/api/v1/topcoder/login",
        data: {
          email: this.email,
          password: this.password
        }
      };
      try {
        const login = await axios(config);
        this.$store.commit("login", login.data);
        this.error = ""
      } catch (err) {
        console.error(err);
        this.error = "Login Failed, Check you password/email";
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../scss/variables";
.login-wrapper {
  height: 100vh;
  width: 100vw;
  background: url("../../assets/imgs/loginBackground.jpg") no-repeat center
    center / cover;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.logo-wrap {
  height: 80px;
}
h1 {
  color: $secondary;
  font-size: 2rem;
  font-family: $font-secondary;
  padding: 20px;
}
.login {
  background-color: #00000085;
  color: $font-light;
  margin-left: 80px;
  min-width: 500px;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  .login-form {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .input-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 10px;
    input {
      border-radius: 3px;
      border: none;
      font-size: $font-size-medium;
      padding: 10px 5px 2px 5px;
      margin-top: 10px;
    }
  }
  .error {
    color: $red;
    text-align: center;
  }
  .login-button {
    font-family: $font-main;
    color: $font-light;
    background-color: $secondary;
    font-weight: $font-weight-heavy;
    font-size: $font-size-medium;
    margin-top: 20px;
    padding: 10px 15px;
    border: none;
    border: 1px solid $black;
    border-radius: 3px;
    &:disabled {
      display: none;
    }
  }
}
</style>
