import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userName: "",
    permissions: "",
    fileName: "",
    IRNSearch: "",
  },
  mutations: {
    login(state, payload) {
      state.userName = payload.email;
      state.permissions = payload.permissions;
      state.fileName = "";
      state.IRNSearch = "";
    },
    logout(state) {
      state.permissions = "";
      state.userName = "";
      state.fileName = "";
      state.IRNSearch = "";
    },
    docName(state, payload) {
      state.fileName = payload.fileName;
    },
    quickSearch(state, payload) {
      console.log(payload)
      state.IRNSearch = payload;
    },
  },
  actions: {},
  modules: {},
});
