<template>
  <nav>
    <form class="searchbar-form" @submit.prevent="quickSearch()">
      <input type="text" placeholder="QuickSearch by IRN" v-model="IRN" />
      <button class="search-button">
        <v-icon name="search" />
      </button>
    </form>
    <div class="logout-wrapper">
      <button @click="logout">Logout</button>
    </div>
    <!-- <div class="routes">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>-->
  </nav>
</template>

<script>
export default {
  name: "NavBar",
  data() {
    return {
      IRN: "",
    };
  },
  methods: {
    logout() {
      this.$store.commit("logout");
    },
    quickSearch() {
      console.log(this.IRN);
      this.$store.commit("quickSearch", this.IRN);
      if (this.$router.currentRoute.path !== "/deep-search") {
        this.$router.push({ path: "/deep-search" });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/scss/variables";
nav {
  width: 100%;
  background-color: $dark-gray;
  color: $font-light;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.routes {
  padding-right: 10px;
}
.searchbar-form {
  display: flex;
  align-items: center;
}
input {
  padding: 5px;
  color: $font-light;
  background: none;
  border: none;
  border-bottom: 1px solid $font-light;
}
input::placeholder {
  color: darken($font-light, 20%);
}
input:focus {
  outline-width: thin;
}
button {
  border: none;
  padding: 5px 10px;
  background-color: $gray;
  cursor: pointer;
}
.search-button {
  border-radius: 3px;
  margin-left: 5px;
}
.dropdown-button {
  border-radius: 0 3px 3px 0;
}
button:hover {
  background-color: lighten($gray, 20%);
  border: 2px solid $black;
  padding: 3px 8px;
}
button:active {
  background-color: darken($font-light, 20%);
}
.logout-wrapper {
  button {
    border-radius: 3px;
    margin-right: 5px;
  }
}
</style>
