import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import Vuelidate from "vuelidate";

import 'vue-awesome/icons/folder';
import 'vue-awesome/icons/folder-open';
import 'vue-awesome/icons/search';
import 'vue-awesome/icons/file';
import 'vue-awesome/icons/file-upload';
import 'vue-awesome/icons/network-wired';
import 'vue-awesome/icons/tree';
import 'vue-awesome/icons/archive';
import 'vue-awesome/icons/angle-double-left';
import 'vue-awesome/icons/angle-double-right';
import 'vue-awesome/icons/angle-right';
import 'vue-awesome/icons/angle-left';
import 'vue-awesome/icons/inbox';
import 'vue-awesome/icons/star';
import 'vue-awesome/icons/heart';
import 'vue-awesome/icons/warehouse';
import 'vue-awesome/icons/caret-down';
import 'vue-awesome/icons/caret-right';
import 'vue-awesome/icons/server';
import 'vue-awesome/icons/plus-square';
import 'vue-awesome/icons/minus-square';
import 'vue-awesome/icons/cloud-download-alt';
import 'vue-awesome/icons/times';

import Icon from 'vue-awesome/components/Icon';

Vue.component('v-icon', Icon);
Vue.use(Vuelidate);

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
