import Vue from "vue";
import VueRouter from "vue-router";
import DeepSearch from "../views/DeepSearch.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/deep-search",
    name: "DeepSearch",
    component: DeepSearch,
  },
  {
    path: "/upload-docs",
    name: "UploadDocs",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/UploadDocs.vue"),
  },
  {
    path: "/inbox",
    name: "Inbox",
    component: () => import("../views/Inbox.vue"),
  },
  {
    path: "/work-queue-monitor",
    name: "WorkQueueMonitor",
    component: () => import("../views/WorkQueueMonitor.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
