<template>
  <div>
    <LoadingSpinner v-if="isLoading" :size="100" />
    <template v-else>
      <form class="search-form" @submit.prevent="searchByIRN">
        <p>IRN:</p>
        <input type="text" v-model="IRN" />
        <input class="button" type="submit" value="Search" />
      </form>
      <p v-if="searchedValue">
        Searched Value: {{ searchedValue }}
        <button @click="clear()">
          <v-icon name="times" />
        </button>
      </p>
      <Table :files="searchedFiles" />
    </template>
  </div>
</template>

<script>
import axios from "axios";
import Table from "./Table";
import LoadingSpinner from "../UI/LoadingSpinner";
export default {
  name: "SearchMain",
  components: {
    Table,
    LoadingSpinner,
  },
  data() {
    return {
      files: [],
      IRN: "",
      searchedValue: "",
      isLoading: true,
    };
  },
  async mounted() {
    this.isLoading = true;
    try {
      const trans = await axios.get(
        "/api/v1/topcoder/documents/Transcripts"
      );
      const isirs = await axios.get(
        "/api/v1/topcoder/documents/ISIRS"
      );
      const allData = await trans.data.Items.concat(isirs.data.Items);
      this.files = await this.files.concat(allData);
      console.log("hey", this.$store.state.IRNSearch);
      this.IRN = this.$store.state.IRNSearch;
      this.searchedValue = this.$store.state.IRNSearch;
      this.isLoading = false;
    } catch (err) {
      console.error(err);
      this.isLoading = false;
    }
  },
  computed: {
    searchedFiles() {
      if (!this.searchedValue) return this.files;
      const matchedIRN = this.files.filter((file) => {
        return file.formData.IRN == this.searchedValue;
      });
      return matchedIRN;
    },
    irnCheck() {
      return this.$store.state.IRNSearch;
    },
  },
  watch: {
    irnCheck(newValue, oldValue) {
      this.IRN = newValue;
      this.searchedValue = newValue;
      this.searchByIRN();
    },
  },
  methods: {
    searchByIRN() {
      this.searchedValue = this.IRN;
    },
    clear() {
      (this.searchedValue = ""),
        (this.IRN = ""),
        this.$store.commit("quickSearch", "");
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/scss/variables";
.search-form {
  display: flex;
  align-items: center;
  p {
    margin: 5px 10px;
  }
  input {
    width: 100px;
    margin-right: 10px;
  }
  .button {
    border: none;
    border-radius: 5px;
    padding: 4px 5px;
    color: $font-light;
    background-color: $font-dark;
    cursor: pointer;
    &:hover {
      background-color: darken($font-dark, 20%);
    }
  }
}
</style>
