<template>
  <nav>
    <div class="logo-and-text">
      <span class="logo-wrap">
        <uopxLogo />
      </span>
      <h1 class="text">UoPX DocuMynt</h1>
    </div>
    <h2>Welcome {{userName.split(".")[0]}}!</h2>
    <div
      class="selection-wrapper"
      :class="{ 'active-route': this.$route.path.includes('/deep-search') }"
      @click="changeRoute('detailSearch')"
    >
      <v-icon name="search" />
      <div>Deep Search</div>
    </div>
    <div
      class="selection-wrapper"
      :class="{ 'active-route': this.$route.path.includes('/inbox') }"
      @click="changeRoute('inbox')"
    >
      <v-icon name="inbox" />
      <div>Inbox</div>
    </div>
    <div
      class="selection-wrapper"
      :class="{ 'active-route': this.$route.path.includes('/upload-docs') }"
      @click="changeRoute('upload')"
    >
      <v-icon name="file-upload" />
      <div>Upload</div>
    </div>
    <div
      class="selection-wrapper"
      :class="{ 'active-route': this.$route.path.includes('/work-queue-monitor') }"
      @click="changeRoute('work-queue-monitor')"
    >
      <v-icon name="network-wired" />
      <div>Work Queue Monitor</div>
    </div>
    <div class="nav-break"></div>
    <DocTree />
  </nav>
</template>

<script>
import uopxLogo from "@/assets/svgs/uopxLogo";
import DocTree from "./DocTree";

export default {
  name: "SideBarMain",
  components: {
    uopxLogo,
    DocTree,
  },
  data() {
    return {};
  },
  computed: {
    userName() {
      return this.$store.state.userName;
    },
  },
  methods: {
    changeRoute(newRoute) {
      if (newRoute === "detailSearch" && !this.$route.path.includes('/deep-search')) {
        this.$router.push({ name: "DeepSearch" });
      } else if (newRoute === "inbox" && !this.$route.path.includes('/inbox')) {
        this.$router.push({ name: "Inbox" });
      } else if (newRoute === "upload" && !this.$route.path.includes('/upload-docs')) {
        this.$router.push({ name: "UploadDocs" });
      } else if (newRoute === "work-queue-monitor" && !this.$route.path.includes('/work-queue-monitor')) {
        this.$router.push({ name: "WorkQueueMonitor" });
      } else {
        return;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/scss/variables";
nav {
  background-color: $dark-gray;
  color: $font-light;
  width: 100%;
  height: 100%;
}
.logo-and-text {
  display: flex;
  align-items: center;
  padding-left: 5px;
}
.logo-wrap {
  padding: 5px;
  width: 80px;
  height: 80px;
}
h1 {
  font-family: $font-secondary;
  font-size: $font-size-small;
  color: $secondary;
  align-self: flex-end;
  padding-bottom: 5px;
}
h2 {
  font-size: $font-size-small;
  text-align: center;
}
.active-route {
  border-left: 5px solid $secondary;
  color: $secondary;
}
.selection-wrapper {
  display: flex;
  align-items: center;
  padding: 10px 0px 10px 5px;
  &:hover {
    cursor: pointer;
    border-left: 5px solid $secondary;
  }
  div {
    margin-left: 5px;
  }
}
.nav-break {
  border: 1px solid $gray;
  margin: 10px 5px 10px 0;
}
</style>
